import { TYPE_COLOR } from "styles/color";

export const ConvertTypeEnglish = (type: string) => {
  let englishType: keyof typeof TYPE_COLOR | "ERROR";
  switch (type) {
    case "":
      englishType = "NORMAL";
      break;
    case "":
      englishType = "FIRE";
      break;
    case "":
      englishType = "WATER";
      break;
    case "":
      englishType = "ELECTRIC";
      break;
    case "":
      englishType = "GRASS";
      break;
    case "":
      englishType = "ICE";
      break;
    case "":
      englishType = "FIGHTING";
      break;
    case "":
      englishType = "POISON";
      break;
    case "":
      englishType = "GROUND";
      break;
    case "":
      englishType = "FLYING";
      break;
    case "":
      englishType = "PSYCHIC";
      break;
    case "":
      englishType = "BUG";
      break;
    case "":
      englishType = "ROCK";
      break;
    case "":
      englishType = "GHOST";
      break;
    case "":
      englishType = "DRAGON";
      break;
    case "":
      englishType = "DARK";
      break;
    case "":
      englishType = "STEEL";
      break;
    case "":
      englishType = "FAIRY";
      break;
    default:
      englishType = "ERROR";
      break;
  }
  return englishType;
};

export const ConvertStatusJapanese = (status: string) => {
  let JapaneseStatus;
  switch (status) {
    case "hp":
      JapaneseStatus = "HP";
      break;
    case "attack":
      JapaneseStatus = "";
      break;
    case "defence":
      JapaneseStatus = "";
      break;
    case "spAttack":
      JapaneseStatus = "";
      break;
    case "spDefence":
      JapaneseStatus = "";
      break;
    case "speed":
      JapaneseStatus = "";
      break;
    default:
      JapaneseStatus = "";
      break;
  }
  return JapaneseStatus;
};
